	module.exports = {"data":{"postsJson":{"title":"Introducción a 1 Pedro ","questionsTitle":null,"display":"none","bookName":"1 Pedro","ref":null,"html":"<h6>Qué hacer en tiempos difíciles</h6><p>¡Eres de la realeza, así que no te rindas!</p><h6>Cómo leer 1 Pedro</h6><p>¿Dónde encuentras esperanza en tiempos de crisis? ¿Dónde puedes encontrar fuerzas para seguir adelante en tiempos difíciles? ¿Qué te hará crecer en fe cuando las dificultades abundan? ¿Cuál es la respuesta? ¡Jesús! A través de lo que Él hizo te ha sido entregada una nueva identidad. Ahora le perteneces a Dios, y eres parte de un «pueblo elegido… sacerdotes del Rey, una nación santa, posesión exclusiva de Dios.» (1Pe 2:9).<br />   Pero, ¿qué pasa con los momentos difíciles? Dios puede y va a usar dificultades para hacernos más fuertes. Descubre cómo la fe, refinada por el sufrimiento, puede ayudarte a ver al Señor más claramente y a conocerlo más íntimamente. Las palabras en esta carta nos animan a permanecer firmes en medio de la dificultad.<br />   Sin embargo, Pedro no se quedó ahí. No se trata solo de sobrevivir. A medida que aprendemos a abrazar nuestra identidad de realeza, podemos ver al reino de Dios crecer, aun frente a la adversidad. Pedro nos insta a adoptar una actitud activa en medio de dificultades, y «mostrar a otros la bondad de Dios, pues él los ha llamado a salir de la oscuridad y entrar en su luz maravillosa.» (1Pe 2:9).</p><h6>¿Quién escribió esta carta y por qué?</h6><p>En algún momento durante la primera mitad de los años 60 d. C., Pedro, uno de los doce discípulos originales, escribió esta carta desde Roma para los creyentes esparcidos por todas las regiones de Asia Menor, actualmente Turquía. Pedro observó que las crecientes dificultades y persecuciones generaron dudas en algunos cristianos, que se preguntaban si Dios los había abandonado. Él les escribió para animarlos, al darles esperanza y sentido en medio de su sufrimiento.</p><h6>¿Cuál es el contexto histórico?</h6><p>En un principio el gobierno romano concedió a los cristianos la misma libertad de culto que a los judíos, pero a medida que aumentó la división entre los judíos y cristianos, la tolerancia hacia el cristianismo se desvaneció. Debido a que la política de Roma prohibía las religiones problemáticas que pudieran suponer una amenaza para la estabilidad del imperio, los cristianos empezaron a sufrir discriminación, violencia, arresto y confiscación de propiedades. Algunos comenzaron a dudar de su fe. El propio Pedro fue encarcelado y golpeado a causa de su fe; teniendo la autoridad genuina para abordar el tema del sufrimiento.</p><h6>Perspectiva de SourceView</h6><p>La carta de Pedro para animar a los creyentes en sufrimiento es sólo una parte de una conversación en curso; por eso todo el texto de SourceView está en negro. La carta está impresa con letra cursiva para darle una sensación de escritura a mano y permitirnos revivir la experiencia de leer una carta divinamente inspirada.<br />","path":"/412","Question1":null,"Question2":null,"Question3":null,"Question4":null,"prevPath":"/411","nextPath":"/413"}},"pathContext":{}}

